9. Quyền hạn pháp lý<br><br>
1. Thỏa thuận này sẽ được điều chỉnh và hiểu theo luật pháp của Singapore, không liên quan đến việc lựa chọn các nguyên tắc luật. Mọi tranh chấp phát sinh từ hoặc liên quan đến Thỏa thuận này, bao gồm bất kỳ câu hỏi nào liên quan đến sự tồn tại, hiệu lực hoặc chấm dứt của Thỏa thuận, sẽ được chuyển đến và giải quyết cuối cùng bằng trọng tài do Trung tâm Trọng tài Quốc tế Singapore quản lý theo Quy tắc Trọng tài của Trọng tài Quốc tế Singapore Trung tâm trong thời điểm hiện tại có hiệu lực, mà các quy tắc được coi là được kết hợp với tham chiếu trong điều khoản này. Trụ sở của bên xét xử sẽ là Singapore. Ngôn ngữ sẽ là tiếng Anh.<br><br>
