var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"privacypolicyPage"},[(_vm.language==0)?_c('div',{staticClass:"strY"},[_vm._v(" Chính sách quyền riêng tư của Buny Live"),_c('br'),_vm._v(" Phát hành vào ngày 30 tháng 6 năm 2019."),_c('br'),_vm._v(" Chúng tôi nhận ra rằng quyền riêng tư là vô cùng quan trọng tại BunnyLive. Chính sách Bảo mật này (\"Chính sách\") áp dụng cho tất cả những người sử dụng ứng dụng BunnyLive và các dịch vụ của Buny trên tất cả các nền tảng (\"Sản phẩm\")."),_c('br'),_vm._v(" Chính sách này mà BunyLive (\"chúng tôi\" hoặc \"chúng tôi\") đã tạo ra là để giải thích những thông tin nào về bạn sẽ được thu thập và cách thông tin đó sẽ được sử dụng và tiết lộ. Thông tin của bạn rất có giá trị đối với chúng tôi để chúng tôi có thể cung cấp dịch vụ tương ứng và trải nghiệm người dùng tốt hơn. Trong khi sử dụng BunyLive với sự đồng ý của bạn đối với Chính sách bảo mật này, thông tin về việc sử dụng, thống kê và đầu vào của bạn sẽ được thu thập. Bằng cách sử dụng Sản phẩm của chúng tôi, bạn hiểu và đồng ý rằng chúng tôi cung cấp nền tảng để bạn đăng nội dung, ảnh, giọng nói, nhận xét và các tài liệu khác (\"Nội dung Người dùng\") thông qua Sản phẩm của chúng tôi và chúng tôi chia sẻ nội dung Người dùng một cách công khai."),_c('br'),_vm._v(" A. CHÚNG TÔI THU THẬP THÔNG TIN NÀO?"),_c('br'),_vm._v(" Thông tin đăng ký và hồ sơ. BunyLive cho phép người dùng sử dụng số điện thoại di động hoặc nền tảng của bên thứ ba để đăng ký và tạo tài khoản trong sản phẩm của chúng tôi. Chúng tôi có thể thu thập thông tin cá nhân của bạn mà bạn cung cấp cho BunyLive như tên, giới tính, ngày sinh, tên người dùng, mật khẩu, địa chỉ email, hình ảnh, số điện thoại, v.v. Chúng tôi cũng thu thập thông tin của bạn từ các bên thứ ba có thể được sử dụng để nhận dạng bạn."),_c('br'),_vm._v(" Chúng tôi có thể thu thập thông tin thiết bị của bạn hoặc thông tin mà thiết bị di động của bạn gửi khi sử dụng Sản phẩm của chúng tôi, chẳng hạn như hệ điều hành, ngôn ngữ, Android ID, IOS ID, địa chỉ IP của bạn, tệp nhật ký, v.v."),_c('br'),_vm._v(" Chúng tôi có thể sử dụng Cookie và các công nghệ tương tự để thu thập thông tin về cách bạn sử dụng Sản phẩm và các tính năng của chúng tôi."),_c('br'),_vm._v(" Thông tin bạn chọn để đăng và / hoặc hiển thị công khai. Bạn đăng ảnh, giọng nói hoặc các tài liệu khác của mình trên Sản phẩm của chúng tôi và bạn hiển thị công khai và chia sẻ chúng với những người khác thông qua Sản phẩm của chúng tôi. Những tài liệu do bạn đăng sẽ vẫn nằm trong tầm kiểm soát của bạn và bạn có thể chọn đăng, hiển thị hay xóa."),_c('br'),_vm._v(" Máy chủ của BunyLive sẽ tự động ghi lại hoạt động và thông tin liên quan do bạn tạo khi bạn sử dụng Sản phẩm của chúng tôi. Thông tin nhật ký này có thể bao gồm các liên kết bạn nhấp vào Sản phẩm của chúng tôi, video phát trực tuyến mà bạn xem, ngày truy cập, thời gian dành cho các trang và tính năng của Sản phẩm, các thống kê khác và thông tin Cookie. Chúng tôi cũng có thể thu thập thông tin tương tác với Sản phẩm của chúng tôi từ bạn để biết sự quan tâm của bạn nhằm tùy chỉnh và cải thiện Sản phẩm và các tính năng của chúng tôi."),_c('br'),_vm._v(" Thông tin bạn gửi để liên lạc với chúng tôi. Ví dụ: ý kiến ​​của bạn về ứng dụng và dịch vụ của chúng tôi qua kênh phản hồi của chúng tôi. Thông tin khác như địa chỉ email và tên do bạn cung cấp. Địa chỉ IP hoặc thông tin GPS của bạn có thể được thu thập khi sử dụng Sản phẩm của chúng tôi để chúng tôi có thể cung cấp các tính năng hoặc dịch vụ cụ thể hơn. Bạn có thể ngăn chúng tôi thu thập địa chỉ IP hoặc thông tin GPS của bạn bằng cách tắt dịch vụ vị trí của bạn, nhưng một số tính năng hoặc dịch vụ sẽ không khả dụng nữa."),_c('br'),_vm._v(" B. CHÚNG TÔI SỬ DỤNG THÔNG TIN THU THẬP ĐỂ LÀM GÌ?"),_c('br'),_vm._v(" Chúng tôi chỉ sử dụng thông tin của bạn để phục vụ bạn tốt hơn như sau,"),_c('br'),_vm._v(" Để cá nhân hóa trải nghiệm người dùng."),_c('br'),_vm._v(" Để cung cấp, duy trì, cải tiến và phát triển các dịch vụ của chúng tôi."),_c('br'),_vm._v(" Để giới thiệu cho những người dùng khác về Nội dung Người dùng hoặc các tính năng mà bạn có thể quan tâm."),_c('br'),_vm._v(" Để vận hành, duy trì và cung cấp các tính năng và chức năng của BunyLive cũng như giao tiếp trực tiếp với bạn, chẳng hạn như tin nhắn email và thông báo đẩy. Để chia sẻ những sáng tạo của bạn trên BunyLive với những người liên hệ của bạn hoặc trên các trang mạng xã hội. Chúng tôi cũng có thể gửi cho bạn email hoặc tin nhắn liên quan đến dịch vụ (ví dụ: xác minh tài khoản, xác nhận đơn đặt hàng, thay đổi hoặc cập nhật các tính năng của BunyLive, thông báo kỹ thuật và bảo mật)."),_c('br'),_vm._v(" C. THÔNG TIN CỦA BẠN CÓ ĐƯỢC CÔNG BỐ KHÔNG?"),_c('br'),_vm._v(" Chúng tôi sẽ không tiết lộ thông tin của bạn cho bất kỳ bên nào không kiểm soát, kiểm soát hoặc dưới sự kiểm soát chung của BunyLive. Chúng tôi sẽ không bán thông tin của bạn cho bất kỳ bên thứ ba nào."),_c('br'),_vm._v(" D. TRANG WEB BÊN THỨ BA"),_c('br'),_vm._v(" Các ứng dụng, dịch vụ và trang web của chúng tôi có thể truy cập thông tin của bên thứ ba (chẳng hạn như thông tin tài khoản Facebook hoặc Twitter của bạn) thông qua các giao diện ứng dụng. Chúng tôi có thể cung cấp các liên kết đến các trang web của bên thứ ba, như một dịch vụ cho Người dùng của chúng tôi. Trang web cũng có thể mang quảng cáo từ các công ty khác. Khi bạn nhấp vào liên kết của các bên thứ ba trong ứng dụng, dịch vụ hoặc trang web, bạn có tùy chọn rời khỏi ứng dụng, dịch vụ hoặc Trang web. Một số trang web của bên thứ ba này có thể được đồng thương hiệu với tên / biểu tượng của chúng tôi hoặc tên / biểu tượng của tổ chức liên kết của chúng tôi, ngay cả khi chúng không được chúng tôi vận hành hoặc duy trì. Chính sách này không đề cập đến và chúng tôi không chịu trách nhiệm về các thực tiễn bảo mật của các trang Web do bên thứ ba điều hành, cho dù chúng được liên kết đến hoặc có thể truy cập được từ Ứng dụng, Dịch vụ hoặc Trang web. Việc bao gồm liên kết hoặc khả năng truy cập của các Trang web của bên thứ ba không có nghĩa là chúng tôi đã chứng thực Trang web của bên thứ ba đó."),_c('br'),_vm._v(" E. NGƯỜI DÙNG BẠN CHIA SẺ THÔNG TIN CỦA MÌNH."),_c('br'),_vm._v(" Chúng tôi không thể kiểm soát hành động của những người dùng khác mà bạn chia sẻ thông tin của mình. Chúng tôi không thể và không kiểm soát thông tin bạn chia sẻ với những người dùng khác bằng ứng dụng của chúng tôi, các dịch vụ hoặc Trang web (bao gồm cả qua Diễn đàn) hoặc cách những Người dùng khác sẽ sử dụng hoặc chia sẻ thông tin đó. Chúng tôi không chịu trách nhiệm về việc bên thứ bạn phá vỡ các biện pháp bảo mật của chúng tôi."),_c('br'),_vm._v(" F. BẢO MẬT"),_c('br'),_vm._v(" BunyLive cực kỳ quan tâm đến việc bảo vệ tính bí mật của thông tin của bạn. Chúng tôi không thu thập thông tin cá nhân. Chúng tôi sử dụng các biện pháp hành chính, vật lý và điện tử được thiết kế để bảo vệ thông tin phi cá nhân của bạn khỏi bị truy cập và sử dụng trái phép. Xin lưu ý rằng không có biện pháp bảo mật nào mà chúng tôi thực hiện để bảo vệ thông tin của bạn được đảm bảo tuyệt đối để tránh việc truy cập trái phép hoặc sử dụng thông tin phi cá nhân của bạn mà không thể xâm nhập. G. THÔNG TIN NHẠY CẢM"),_c('br'),_vm._v(" Chúng tôi yêu cầu bạn không gửi cho chúng tôi hoặc tiết lộ bất kỳ thông tin cá nhân nhạy cảm nào (ví dụ: thông tin liên quan đến chủng tộc, nguồn gốc dân tộc, chính kiến, tôn giáo, tín ngưỡng khác, sức khỏe, khuynh hướng tình dục, lý lịch tội phạm, tư cách thành viên trong các tổ chức trước đây bao gồm cả tư cách thành viên công đoàn) trên hoặc thông qua ứng dụng, dịch vụ và trang web của chúng tôi, hoặc với chúng tôi."),_c('br'),_vm._v(" H. NGƯỜI DÙNG QUỐC TẾ"),_c('br'),_vm._v(" Thông tin của bạn có thể được lưu trữ và xử lý tại Hoa Kỳ, nơi chúng tôi có cơ sở vật chất và bằng cách sử dụng ứng dụng của chúng tôi, các dịch vụ hoặc trang web mà bạn đồng ý cho việc chuyển thông tin của bạn đến Hoa Kỳ, có thể nằm ngoài quốc gia cư trú của bạn và có thể cung cấp các quy tắc bảo vệ dữ liệu khác và ít nghiêm ngặt hơn ở quốc gia của bạn. Nếu bạn phản đối việc thông tin của bạn được chuyển giao hoặc sử dụng như được mô tả trong Chính sách này, vui lòng không sử dụng ứng dụng, dịch vụ hoặc trang web của chúng tôi và xóa ngay tất cả các ứng dụng khỏi thiết bị của bạn. I. LIÊN HỆ VỚI CHÚNG TÔI / BÁO CÁO VI PHẠM Nếu bạn có bất kỳ câu hỏi hoặc nhận xét nào về Chính sách này hoặc các thông lệ về quyền riêng tư của chúng tôi, hoặc để báo cáo bất kỳ vi phạm Chính sách hoặc lạm dụng ứng dụng, dịch vụ của chúng tôi hoặc trang web, vui lòng liên hệ với chúng tôi tại bunycompany@gmail.com. J. THAY ĐỔI CHÍNH SÁCH QUYỀN RIÊNG TƯ NÀY Chính sách Bảo mật của chúng tôi có thể thay đổi theo thời gian, chúng tôi sẽ đăng bất kỳ thay đổi nào trên trang này, vì vậy vui lòng kiểm tra định kỳ. Chúng tôi có thể cung cấp cho bạn các hình thức thông báo bổ sung về các sửa đổi hoặc cập nhật nếu thích hợp trong các trường hợp. Nếu bạn không đồng ý với bất kỳ sửa đổi nào đối với Chính sách này, cách duy nhất của bạn là ngừng ngay lập tức tất cả việc sử dụng tất cả Ứng dụng, Dịch vụ và Trang web. Việc bạn tiếp tục sử dụng bất kỳ Ứng dụng, Dịch vụ hoặc Trang web nào sau khi đăng bất kỳ sửa đổi nào đối với Chính sách này sẽ cấu thành việc bạn chấp nhận Chính sách đã sửa đổi. Xin lưu ý rằng không nhân viên hoặc đại lý nào của chúng tôi có quyền thay đổi bất kỳ Chính sách nào của chúng tôi. ")]):_vm._e(),(_vm.language==1)?_c('div',{staticClass:"strE"},[_vm._v(" Buny Live Privacy Policy"),_c('br'),_vm._v(" Released on June 30, 2019."),_c('br'),_vm._v(" We recognize that privacy is extremely important at BunnyLive. This Privacy Policy (\"Policy\") applies to everyone who uses the BunnyLive application and its services on all platforms (the \"Product\")."),_c('br'),_vm._v(" This Policy that BunyLive (\"us\" or \"we\") has created is to explain what information about you will be collected, and how it is going to be used and disclosed. Your information is very valuable to us so that we can provide corresponding service and better user experience. While using BunyLive with your consent of this Privacy Policy, information on your usage, statistics and input will be collected. By using our Product you understand and agree that we provide a platform for you to post contents, photos, voices, comments and other materials (\"User Content\") through our Product and we share User Content publicly."),_c('br'),_vm._v(" A. WHAT INFORMATION DO WE COLLECT?"),_c('br'),_vm._v(" Registration and profile information. BunyLive allows users to use mobile number or a third party platform to register and create an account in our product. We may collect your personal information you provide to BunyLive such as your name, gender, date of birth, username, password, email address, picture, phone number and so on. We also collect your information from the third parties which can be used to identify you."),_c('br'),_vm._v(" We may collect your device information or the information that your mobile device sends when using our Product such as its operation system, language, Android ID, IOS ID, your IP address, log files and so on."),_c('br'),_vm._v(" We may use Cookies and similar technologies to collect information about how you use our Product and features."),_c('br'),_vm._v(" Information you choose to post and/or display publicly. You post your photo, voice or other materials on our Product, and you publicly display and share them with others through our Product. These materials posted by you would still be under your control and you can choose whether you post, display or remove."),_c('br'),_vm._v(" BunyLive’s server will automatically record the activity and related information created by you when you use our Product. This log information may include the links you click on our Product, streaming videos that you watch, access date, time spent on pages and features of the Product, other statistics and Cookie information. We may also collect the interaction information with our Product from you to know your interest in order to customize and improve our Product and features."),_c('br'),_vm._v(" The information you submit to communicate with us. For example, your opinions on our application and services via our feedback channel. Other information such as email address and names provided by you."),_c('br'),_vm._v(" Your IP address or GPS information may be collected when using our Product so we can provide more specified feature or services. You could stop us from collecting your IP address or GPS information by turning off your location services, but some features or services will not be available anymore."),_c('br'),_vm._v(" B. WHAT DO WE USE THE COLLECTED INFORMATION FOR?"),_c('br'),_vm._v(" We use your information only for serving you better as the following,"),_c('br'),_vm._v(" To personalize user experience."),_c('br'),_vm._v(" To provide, maintain, improve and develop our services. To recommend the other users about the User Content or features you may be interested."),_c('br'),_vm._v(" To operate, maintain, and provide the features and functions of BunyLive and to communicate directly with you, such as email messages and push notifications. To share your creations on BunyLive with your contacts or on social media sites. We may also send you service-related emails or messages (e.g., account verification, order confirmations, change or updates to features of BunyLive, technical and security notices)."),_c('br'),_vm._v(" C. WILL YOUR INFORMATION BE DISCLOSED? We will not disclose your information to any party which is not controlling, controlled or under common control of BunyLive. We will not sell your information to any third party."),_c('br'),_vm._v(" D. THIRD- PARTY WEBSITES"),_c('br'),_vm._v(" Our applications, services and site may access third party information (such as your Facebook or Twitter account information) through application interfaces. We may provide links to third-party websites, as a service to our Users. The Site may also carry advertisements from other companies. When you click on the links of third parties in an application, the services or the site, you have the option to leave the application, the services or the Site. Some of these third party sites may be co-branded with our name/logo or our affiliated entity's name/logo, even though they are not operated or maintained by us. This Policy does not address, and we are not responsible for, the privacy practices of Web sites operated by third parties, whether they are linked to or otherwise accessible from an Application, the Services or the Site. The inclusion of a link or accessibility of third party Websites does not imply endorsement of such third party Website by us."),_c('br'),_vm._v(" E. USERS YOU SHARE YOUR INFORMATION WITH. We cannot control the actions of other users you share your information with. We cannot and do not control the information you share with other users using our application, the services or the Site (including via Forums) or how such other Users will use or share such information. We are not responsible for third party circumvention of our security measures."),_c('br'),_vm._v(" F. SECURITY"),_c('br'),_vm._v(" BunyLive is extremely concerned about safeguarding the confidentiality of your information. We do not collect personal information. We employ administrative, physical and electronic measures designed to protect your non-personal information from unauthorized access and use. Please be aware that no security measures that we take to protect your information is absolutely guaranteed to avoid unauthorized access or use of your non-personal information which is impenetrable."),_c('br'),_vm._v(" G. SENSITIVE INFORMATION"),_c('br'),_vm._v(" We ask you not to send us or disclose any sensitive personal information (e.g., information related to racial, ethnic origin, political opinions, religion, other beliefs, health, sexual orientation, criminal background, membership in past organizations including trade union memberships) on or through our application, services and the site, or otherwise to us."),_c('br'),_vm._v(" H. INTERNATIONAL USERS"),_c('br'),_vm._v(" Your information may be stored and processed in the United States where we have facilities, and by using our application, the services or the site you consent to the transfer of your information to the United States, which may be outside of your country of residence and may provide for different and less stringent data protection rules than in your country. If you object to your information being transferred or used as described in this Policy, please do not our application, services or the site and immediately delete all applications from your devices."),_c('br'),_vm._v(" I. CONTACT US / REPORT VIOLATIONS"),_c('br'),_vm._v(" If you have any questions or comments about this Policy or our privacy practices, or to report any violations of the Policy or abuse of our application, services or the site, please contact us at bunycompany@gmail.com."),_c('br'),_vm._v(" J. CHANGES TO THIS PRIVACY POLICY"),_c('br'),_vm._v(" Our Privacy Policy may change from time to time, we will post any changes on this page, so please review it periodically. We may provide you additional forms of notice of modifications or updates as appropriate under the circumstances."),_c('br'),_vm._v(" If you do not agree to any modifications to this Policy, your sole recourse is to immediately stop all use of all Applications, the Services and the Site. Your continued use of any Application, the Services or the Site following the posting of any modifications to this Policy will constitute your acceptance of the revised Policy. Please note that none of our employees or agents has the authority to vary any of our Policies."),_c('br')]):_vm._e()])}
